#curtain {
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    font-size: 25px;

    width: calc(100% - 28px);
    height: calc(100% - 14px);
    z-index: 1000;
    position: absolute;
    top: 0;
}

#curtain span {
    display: block;
    position: absolute;
    top: 49%;
    width: 100%;
    text-align: center;
}

.entry {
    position: relative;
}

.chart {
    width: 100%;
    height: 400px;
}