.app-wrapper {
  width: 100%;
  height: 100vh;
  overflow: hidden;

  padding: 10px;
  background-color: rgb(255, 255, 255);

  display: flex;
  flex-direction: row;
  align-items: stretch;
}

.charts-stack-wrapper {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  margin: 0!important;
}

.left-column {
  display: flex;
  flex-direction: column;
  width: 30%;
  height: 100%;
  margin-right: 10px;
}

.right-column {
  display: flex;
  width: 70%;
  height: 100%;
}

.tree-wrapper {
 width: 100%;
  height: 60%;
  margin-bottom: 10px;
}

.alerts-wrapper {
  width: 100%;
  height: 40%;
}

.tabs-wrapper {
  width: 100%;
  height: 100%;
}

i.icon.angle.right:before {
  color: #2185d0;
}

