.custom-tab {
    height: calc(100% - 52px);
    box-shadow: 0 2px 4px 0 rgba(34, 36, 38, .12), 0 2px 10px 0 rgba(34, 36, 38, .15) !important;
    margin-top: 10px !important;
    padding: 0!important;
    border: 0!important;
}

.tabs-menu {
    box-shadow: 0 2px 4px 0 rgba(34,36,38,.12), 0 2px 10px 0 rgba(34,36,38,.15)!important;
    margin-bottom: 10px!important;
}

.tabs-block {
    width: 100%;
    height: 100%;
}